import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Footer, Home, Navigation, Projects } from "./components";
export default class App extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    var data = [10, 20, 30];

    return (
      <div className="App">
        <Router>
          <Navigation />
          <Switch>
            <Route path="/" exact component={() => <Home />} />
            <Route path="/projects" exact component={() => <Projects data={data}/>} />
          </Switch>
          <Footer />
        </Router>
      </div>
    );
  }
}

